import React from 'react';
import { List, Card } from 'antd';
import item3 from '../images/item-3.jpg';
import {getItemsByUserId} from '../utils/utils';
import {Link} from "react-router-dom";

const data = [
    {
        title: 'Item 1',
    },
    {
        title: 'Item 2',
    },
    {
        title: 'Item 3',
    },
    {
        title: 'Item 4',
    },
];

class ItemSale extends React.Component{
    render(){
        return(
            <div>
                <div><h1>Items for Sale</h1></div>
                <div>
                    <Link to="/AllItemsPage/" >See All Items
                    </Link>
                </div>
                <div>
                <List
                    grid={{ gutter: 16, column: 4 }}
                    dataSource={data}
                    renderItem={item => (
                        <List.Item>
                            <Card title={item.title}><a href="http://www.google.com"><img src={item3} /></a></Card>
                        </List.Item>
                    )}
                />
               </div>

            </div>

        );
    }
}

export default ItemSale;


