import React from "react";
import ReactDOM from "react-dom";
import "antd/dist/antd.css";
import "../index.css";
import { List, Typography, Button } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import {Link} from "react-router-dom";
import Main from "./Main";

const { Title } = Typography;
const listData = [];

for (let i = 0; i < 15; i++) {
  listData.push({
    href: "https://ant.design",
    title: "Casio label writer disc title printer CW-E60 16039 fromJAPAN",
    image:
      "https://images-na.ssl-images-amazon.com/images/I/81OWCPMmCJL._AC_SL1500_.jpg"
  });
}

class AllItemsPage extends React.Component{
    render(){
        return(
            <div>
                <Title>All Items</Title>
                <List
                    itemLayout="vertical"
                    size="large"
                    pagination={{
                        onChange: (page) => {
                            console.log(page);
                        },
                        pageSize: 4
                    }}
                    dataSource={listData}
                    footer={
                        <div>
                            <b>ant design</b> footer part
                        </div>
                    }
                    renderItem={(item) => (
                        <List.Item
                            key={item.title}
                            extra={<img width={150} alt="item" src={item.image} />}
                            actions={[
                                <Button
                                    type="primary"
                                    icon={<DeleteOutlined />}
                                    style={{ background: "orange", borderColor: "orange" }}
                                >
                                    Delete Item
                                </Button>
                            ]}
                        >
                            <List.Item.Meta title={<a href={item.href}>{item.title}</a>} />
                        </List.Item>
                    )}
                />

            </div>

        );
    }
}

export default AllItemsPage;


